/* TODO: make responsive */

:root {
  --black: #222;
  --blue: #5c9cbc;
  --blue-light: #0abde3;
  /* --grey: #dfe6e9; */
  --grey: #c8c8c8;
  --light-grey: #FAFAFA;
  --dark-grey: #7f8c8d;
  --mid-grey: #f3f3f3;
  --mid-grey1: #eaeaea;
  --mid-grey2: #e2e2e2;
  --background-blue: rgba(92,156,188, 0.1);
}

/* @font-face {
  font-family: 'PostGroteskMedium';
  font-style: normal;
  font-weight: 400;
  src: url('/assets/fonts/post-grotesk/PostGrotesk-AllStyles-WEBFONTS/PostGrotesk-Medium.eot');
